









































































































































































































































































































































































































import { Component, Vue, Prop, Ref } from 'vue-property-decorator';
import {
  tempTask,
  listTag,
  getUserInfoByCode,
  userDicts
} from '@api/mainPages';
@Component({
  components: {}
})
export default class TemporaryTask extends Vue {
  @Ref() workRuleForm: any;
  @Prop({ default: false }) temporaryDialog: boolean;
  private sponsorWayShow = false;
  private onceWayShow = false;
  private moreHide = true;
  private moreShow = false;
  private telShow = false;
  private emailShow = false;
  private wechatShow = false;
  private temporaryReset = false;
  private changeId = 0;
  private remindId = 1;
  private sponsorId = 0;
  private frequencyId = 0;
  private userNameData: Array<any> = [];
  private taskTagsData: Array<any> = [];
  private skewingOptions: Array<any> = [
    { value: '1', label: '' },
    { value: '2', label: '' }
  ];
  private frequencyOptions: Array<any> = [
    { value: '2', label: '', id: 0 },
    { value: '3', label: '', id: 1 },
    { value: '4', label: '', id: 2 },
    { value: '5', label: '', id: 3 },
    { value: '6', label: '', id: 4 },
    { value: '7', label: '', id: 5 },
    { value: '8', label: '', id: 6 }
  ];
  private options: Array<any> = [
    { value: '1', label: '' },
    { value: '2', label: '' },
    { value: '3', label: '' },
    { value: '4', label: '' },
    { value: '5', label: '' }
  ];
  private remindOptions: Array<any> = [
    { value: '1', label: '', id: 1 },
    { value: '2', label: '', id: 2 },
    { value: '3', label: '', id: 3 },
    { value: '4', label: '', id: 4 }
  ];
  private sponsorDateOptions: Array<any> = [
    { value: '1', label: '', id: 0 },
    { value: '2', label: '', id: 1 }
  ];
  private taskFrequencyOptions: Array<any> = [
    { value: '1', label: '', id: 0 },
    { value: '2', label: '', id: 1 }
  ];
  private timeoutSetOptions: Array<any> = [
    { value: '1', label: '1', id: 0 },
    { value: '2', label: '5', id: 1 },
    { value: '3', label: '24', id: 2 },
    { value: '4', label: '48', id: 3 }
  ];
  private launchWayOptions: Array<any> = [
    { value: '1', label: '', id: 0 },
    { value: '2', label: '', id: 1 },
    { value: '3', label: '', id: 2 }
  ];
  private ruleForm: any = {
    temporaryName: '', //  string
    temporaryDesc: '', //  string
    checkPossess: false, // 
    checkMonitor: false, // 
    taskFrequency: '', // 
    sponsorWayNum: 1, //  int
    temporaryStartTime: new Date(), //  string
    temporaryEndTime: new Date(),
    temporaryHandler: '', //  string
    temporaryResource: '0', //  int
    temporaryTally: '', //  string
    temporaryCont: '', //  string
    temporaryType: '', //  string
    frequencyNum: '0', //  string
    temporaryTime: 0, //  
    timeoutDay: 0, //  int
    timeoutHour: 0,
    launchWay: '',
    phoneNumber: '',
    addressee: '',
    copyTo: '',
    wechat: '',
    sponsorStartTime: new Date(),
    sponsorEndTime: new Date(),
    sponsorWayDate: 0, //  
    temporarySkewingTime: 0, //  
    skewingTime: '',
    temporaryNum: 0, //  int
    skewingNum: 0,
    temporaryTitle: '' // 
  };
  private rules: any = {
    temporaryName: [
      { required: true, message: '', trigger: 'blur' }
    ]
  };
  // 
  moreShowBtn() {
    this.moreHide = false;
    this.moreShow = true;
    this.taskTagsList();
    this.itemSceneUser();
  }
  // 
  taskTagsList() {
    listTag().then((res) => {
      if (res.data) {
        this.taskTagsData = res.data;
      }
    });
  }
  //  
  itemSceneUser() {
    const userCode = this.$store.state.user.userInfo.usercode;
    const userCodeName = userCode.split(',');
    getUserInfoByCode(userCodeName).then((res) => {
      if (res.data) {
        this.userName = res.data.rows[0].deptId;
        userDicts({ deptId: this.userName }).then((res) => {
          if (res.data) {
            this.userNameData = res.data;
          } else {
            this.$message.error('' + res.error.message);
          }
        });
      }
    });
  }
  // 
  temporaryTaskSave() {
    const params = {
      cc: this.ruleForm.copyTo, //  string
      message: this.ruleForm.temporaryCont, //  string
      messageTitle: this.ruleForm.temporaryTitle, //  string
      offsetUnit: this.sponsorId, //  int
      outTime: this.ruleForm.sponsorWayNum, //  int
      outTimeType: this.sponsorId, //  int
      productCode: '', //  string
      productName: '', //  string
      monitorTask: Number(this.ruleForm.checkMonitor), //  int
      possessTask: Number(this.ruleForm.checkPossess), //  int
      recipient: this.ruleForm.phoneNumber, //  string
      relatedProduct: parseInt(this.ruleForm.temporaryResource), //  int
      remindDay: this.ruleForm.temporaryNum, //  int
      remindFreq: this.ruleForm.frequencyNum, //  string
      remindType: this.remindId, //  int
      remindUnit: this.frequencyId, //  int
      taskEndTime: '',
      taskFreq: this.changeId, //  int
      taskHandler: this.ruleForm.temporaryHandler, //  string
      taskName: this.ruleForm.temporaryName, //  string
      taskNote: this.ruleForm.temporaryDesc, //  string
      taskOffsetUnit: 0, //  int
      taskRemindDay: 0, //  int
      taskRemindFreq: 0, //  int
      taskRemindUnit: 0, //  int
      taskStartTime: this.ruleForm.temporaryStartTime, //  string
      taskTag: this.ruleForm.temporaryTally //  string 
    };
    this.workRuleForm.validate((valid) => {
      if (valid) {
        tempTask(params).then((res) => {
          if (res.data) {
            this.$message.success('');
            this.temporaryDialog = false;
            this.workRuleForm.resetFields();
            this.ruleForm.checkPossess = false;
            this.ruleForm.checkMonitor = false;
            this.telShow = false;
            this.emailShow = false;
            this.wechatShow = false;
            this.$emit('temporaryTaskClick', this.temporaryDialog);
          } else {
            this.$message.error(':' + res.error.message);
          }
        });
      }
    });
  }
  // 
  temporarySaveNext() {
    this.temporaryReset = !this.temporaryReset;
    const params = {
      cc: this.ruleForm.copyTo, //  string
      message: this.ruleForm.temporaryCont, //  string
      messageTitle: this.ruleForm.temporaryTitle, //  string
      offsetUnit: this.sponsorId, //  int
      outTime: this.ruleForm.sponsorWayNum, //  int
      outTimeType: this.sponsorId, //  int
      productCode: '', //  string
      productName: '', //  string
      recipient: this.ruleForm.phoneNumber, //  string
      relatedProduct: parseInt(this.ruleForm.temporaryResource), //  int
      remindDay: this.ruleForm.temporaryNum, //  int
      remindFreq: this.ruleForm.frequencyNum, //  string
      remindType: this.remindId, //  int
      remindUnit: this.frequencyId, //  int
      taskEndTime: '', //  string
      taskFreq: this.changeId, //  int
      taskHandler: this.ruleForm.temporaryHandler, //  string
      taskName: this.ruleForm.temporaryName, //  string
      taskNote: this.ruleForm.temporaryDesc, //  string
      taskOffsetUnit: 0, //  int
      taskRemindDay: 0, //  int
      taskRemindFreq: 0, //  int
      taskRemindUnit: 0, //  int
      taskStartTime: this.ruleForm.temporaryStartTime, //  string
      taskTag: this.ruleForm.temporaryTally //  string 
    };
    this.workRuleForm.validate((valid) => {
      if (valid) {
        tempTask(params).then((res) => {
          if (res.data) {
            this.$message.success('');
            this.$emit('temporaryTaskClick', this.temporaryDialog);
          } else {
            this.$message.error(':' + res.error.message);
          }
        });
      }
    });
  }
  //  
  temporaryResetBtn() {
    this.workRuleForm.resetFields();
    this.ruleForm.checkPossess = false;
    this.ruleForm.checkMonitor = false;
    this.telShow = false;
    this.emailShow = false;
    this.wechatShow = false;
  }
  // 
  temporaryTaskCancel() {
    this.temporaryDialog = false;
    this.workRuleForm.resetFields();
    this.ruleForm.checkPossess = false;
    this.ruleForm.checkMonitor = false;
    this.telShow = false;
    this.emailShow = false;
    this.wechatShow = false;
    this.$emit('temporaryTaskClick', this.temporaryDialog);
  }
  //    
  taskFrequencyChange(item) {
    this.changeId = item.id;
    if (item.id === 1) {
      this.sponsorWayShow = true;
      this.onceWayShow = false;
    } else {
      this.sponsorWayShow = false;
      this.onceWayShow = true;
    }
  }
  //    
  remindChange(item) {
    this.remindId = item.id;
    if (item.id === 1) {
      this.telShow = true;
      this.emailShow = false;
      this.wechatShow = false;
    } else if (item.id === 2) {
      this.telShow = false;
      this.emailShow = true;
      this.wechatShow = false;
    } else if (item.id === 3) {
      this.telShow = false;
      this.emailShow = false;
      this.wechatShow = true;
    } else {
      this.telShow = false;
      this.emailShow = false;
      this.wechatShow = false;
    }
  }
  //  
  frequencyChange(val) {
    this.frequencyId = val;
  }
  // ,    
  sponsorChange(val) {
    this.sponsorId = val.id;
  }
}
