  created() {
    const date = new Date(); //(2020.8.31
    const year = date.getFullYear().toString(); //    '2020'
    const month =
      date.getMonth() + 1 < 10
        ? '0' + (date.getMonth() + 1).toString()
        : (date.getMonth() + 1).toString(); // 0110'0'   '08'
    const day =
      date.getDate() < 10
        ? '0' + date.getDate().toString()
        : date.getDate().toString(); //10'0'   '12'
    const hh =
      date.getHours() < 10
        ? '0' + date.getHours().toString()
        : date.getHours().toString(); // 10'0'   '12'
    const mm =
      date.getMinutes() < 10
        ? '0' + date.getMinutes().toString()
        : date.getMinutes().toString(); // 10'0'   '12'
    const ss =
      date.getSeconds() < 10
        ? '0' + date.getSeconds().toString()
        : date.getSeconds().toString(); //10'0'   '12'
    //
    const end = year + '-' + month + '-' + day + ' ' + hh + ':' + mm + ':' + ss; //'2019-04-12'
    const beg =
      year + '-' + month + '-01' + ' ' + '00' + ':' + '00' + ':' + '00'; //'2019-04-01'
    this.dateValue = [beg, end]; //
  }
